@media (max-width : 900px) {
    .top-nav{
        display: flex;
        align-items: center;
        justify-content: center !important;
    }
    .right-notify{
        display: none !important;
    }
    #mobileNum,#gmailId{
        font-size: 13px !important;
    }
    #top-notify-message{
        display: none;
    }
   .button-container{
        display: none;
    }
    .navbar-container{
        height: 60px !important;
    }
    .nav-container{
        width: 100% !important;
        justify-content: space-between !important;
        align-items: center;
    }
    .logo-container{
        margin-left: -8% !important;
        width: 250px !important;
        height: 110px !important;
    }
    .bars-slider{
        display: block;
    }
    .links{
        background-color: white !important;
        position: fixed;
        justify-content: flex-end;
        align-items: flex-start;
        top: 100%;
        width: 100%;
        border-top: 1px solid rgb(177, 175, 175);
        left: 0%;
        height: 100vh;
        transition: all 0.5s linear;
    }
    #page-links-container{
        flex-direction: column !important;
        padding-top: 50px !important;
        gap: 50px;
    }
    .link{
        color: black;
        font-size: 18px;
        font-weight: 400 !important;
        font-family: 'Rubik',sans-serif !important;
        text-transform: uppercase;
        -webkit-tap-highlight-color: transparent;

    }
    .lessThanSymbol{
        color: rgb(0, 0, 0) !important;
    }
    
    .submenu{
        top: 70% !important;
        z-index: 1000;
    }
    .submenu-link{
        -webkit-tap-highlight-color: transparent;
    }

  
}

@media (max-width : 1000px) {
    .container-find-place{
        flex-direction: column !important;
        gap: 30px !important;
        height: auto !important;
    }
    #find-place-title{
        font-size: 35px !important;
        font-weight: 500 !important;
    }
    .input-bar-fields{
        flex-wrap: wrap !important;
        align-items: normal !important;
    }
    .container-find-place{
        padding: 50px 10px !important;
    }

}

@media (max-width : 544px) {
    .select-option-place,.input-text-field{
        width: 90% !important;
        margin: auto;
        color: rgb(0, 0, 0) !important;
        
    }
    #find-place-title{
        margin-right: 15px !important;
    }
    #datepicker{
        color: rgb(255, 255, 255) !important;
        border: 1px solid rgb(247, 247, 247) !important;
    }
    #title-destination{
        font-size: 35px !important;
        width: 100% !important;
        text-align: center !important;
    }
    #popular-places-heading{
        font-size: 35px !important;
        width: 100% !important;
        text-align: center !important;
    }
    #description-destination{
        width: 100% !important;
    }
    #description-about-places{
        width: 100% !important;
    }
    .destination-paces{
        width: 100% !important;
    }
    .city_popular_place{
        height: 320px !important;
        width: 95% !important;
    }
    .city_popular_place:hover {
        transform: translateY(0px) !important; /* Move the box 20px up when hovering */

    }
    #places_in_city{
        -webkit-tap-highlight-color: transparent;
    }
}
@media (max-width:820px) {
    .user-mail-section{
        flex-direction: column !important;
        height: 300px !important;
    }
    .heading-and-title-mail{
        width: 89% !important;
    }
}

@media (max-width :600px) {
    .mail-input-bar{
        flex-direction: column;
        width: 100% !important;
    }
}
@media (max-width : 480px) {
    .heading-and-title-mail{
        width: 100% !important;
    }
    #email{
        width: 100% !important;
    }
    #text-mail{
        font-size: 15px;
        color: rgb(247, 247, 247);
    }
}